.product-item {
  display: block;
  width: 100%;
  border-radius:10px;
  transition: box-shadow 0.2s;

  &:hover {
    box-shadow: 0 0 15px #6ea8fe;
  }

  &--image {
    height: 200px;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
    background: #f8f9fa;

    img {
      width: 100%;
    }
  }

  &--description {
    padding: 15px;
    color: #6c757d;
    line-height: 1.5;
    font-size: 20px;
    background: #f8f9fa;
    border-radius: 0 0 10px 10px;
    box-shadow: 0 1px 0 0 #dce1e6, 0 0 0 1px #e7e8ec;

    .ant-typography {
      margin: 0;
    }
  }

  &--cost {
    font-size: 18px;
  }
}
